@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Fredoka One", cursive;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.header {
  width: 100%;
  position: fixed;
  background-color: rgb(128, 148, 250);
  z-index: 50;
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 80%;
  margin: 20px auto;
  background-color: transparent;
  height: 50px;
  color: white;
}

.logo {
  font-size: 2.5rem;
}

.navbar ul {
  display: flex;
}

.nav-items {
  list-style-type: none;
  font-size: 1.5rem;
  text-underline-position: none;
  color: white;
}

a:hover {
  background-color: #08ee10;
  border-radius: 5px;
  transition: 0.2s ease-in;
  cursor: pointer;
}

@media screen and (max-width: 925px) {
  .nav-items {
    font-size: 1rem;
  }
}

@media screen and (max-width: 760px) {
  .nav-items {
    font-size: 1.25rem;
  }

  .navbar {
    flex-direction: column;
    margin: 10px auto;
    height: 100%;
    max-width: 60%;
  }

  .header {
    width: 100%;
    margin: 0;
  }

  .logo {
    font-size: 2.5rem;
  }
}
@media screen and (max-width: 440px) {
  .logo {
    font-size: 2rem;
  }

  .nav-items {
    font-size: 1rem;
  }
}

@media screen and (max-width: 360px) {
  .nav-items {
    font-size: 0.75rem;
  }
  .logo {
    font-size: 1.8rem;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.hero-section {
  height: 950px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/header_photo4.0b0e7964.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.title {
  color: white;
  padding-top: 200px;
  font-size: 5rem;
  width: 550px;
  margin: 0 auto;
  z-index: 5;
}

@media screen and (max-width: 1400px) {
  .title {
    font-size: 4.5rem;
  }
}

@media screen and (max-width: 1000px) {
  .title {
    font-size: 4rem;
    max-width: 50%;
  }
}

@media screen and (max-width: 570px) {
  .title {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 440px) {
  .title {
    font-size: 2.25rem;
    max-width: 60%;
  }
}

@media screen and (max-width: 360px) {
  .title {
    padding-top: 200px;
    max-width: 75%;
  }

  .hero-section {
    height: 800px;
  }
}

.about-title {
  padding: 25px 0 20px;
  margin: 20px auto;
  text-align: center;
  font-size: 4rem;
}

.tagline {
  text-align: center;
  font-size: 2rem;
}

hr.about-hr {
  width: 20%;
}

.about-container {
  border-radius: 5px;
  width: 60%;
  margin: 20px auto;
  padding: 10px 0 20px 0;
  text-align: center;
}

.about-container > p {
  margin-top: 70px;
  font-size: 2rem;
}

.about-container ul {
  margin: 10px auto;
  width: 60%;
  text-align: left;
}

.about-container ul li {
  padding: 10px 0;
  font-size: 1.8rem;
  list-style-type: none;
  text-align: center;
  margin-bottom: 30px;
}

.tech {
  text-align: center;
  margin: 50px auto 20px auto;
}

.tech-container {
  max-width: 80%;
  margin: 50px auto 20px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  position: relative;
}

.html img,
.javascript img,
.react img,
.wordpress img {
  height: 150px;
  margin: 5px;
  width: 150px;
}

.css img {
  height: 160px;
  width: 150px;
  position: relative;
  top: -5px;
  margin: 5px;
}

.faIcon {
  position: relative;
  left: 45%;
  margin: 40px auto;
  font-size: 80px;
  color: rgb(128, 148, 250);
}

.faIcon2 {
  left: 47%;
}

.faIcon3 {
  left: 46%;
}

@media screen and (max-width: 810px) {
  .tech-container {
    width: 95%;
    flex-wrap: wrap;
  }

  .about-container {
    width: 80%;
  }
}

@media screen and (max-width: 970px) {
  .faIcon {
    left: 40%;
  }

  .faIcon2 {
    left: 45%;
  }

  .faIcon3 {
    left: 44%;
  }
}

@media screen and (max-width: 600px) {
  .faIcon {
    left: 36%;
  }

  .faIcon2 {
    left: 42%;
  }

  .faIcon3 {
    left: 41%;
  }

  .about-container ul {
    width: 80%;
  }

  .about-container ul li {
    font-size: 1.4rem;
  }
}

hr {
  position: relative;
  top: 50px;
  width: 60%;
  margin: 0 auto;
}

.contact-form-card {
  width: 40%;
  border: 1x solid #333;
  border-radius: 5px;
  background-color: rgb(128, 148, 250);
  margin: 100px auto;
  text-align: center;
  padding: 20px;
  box-shadow: 10px 10px rgba(128, 148, 250, 0.145);
}

.contact-form {
  width: 70%;
  margin: 0 auto;
  text-align: left;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
}

input[type="submit"] {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 20px;
}

input[type="submit"]:hover {
  background-color: #08ee10;
  transition: 0.3s ease-out;
}

@media screen and (max-width: 1000px) {
  .contact-form-card {
    width: 60%;
  }
}

@media screen and (max-width: 670px) {
  .contact-form-card {
    width: 80%;
  }

  .contact-form {
    width: 85%;
  }
}

@media screen and (max-width: 450px) {
  .contact-form-card {
    width: 100%;
  }

  .contact-form {
    width: 95%;
  }
}

hr {
  width: 60%;
  margin: 0 auto;
}

.placeholder {
  padding: 100px 0 20px;
  margin: 0 auto;
  text-align: center;
}

.tagline {
  text-align: center;
  margin: 10px auto;
}

.grid-container {
  max-width: 90%;
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 20px 20px;
  gap: 20px 20px;
}

.grid-item {
  height: 100%;
  position: relative;
  border-radius: 20px;
  transition: transform 0.3s;
  box-shadow: 10px 10px rgba(128, 148, 250, 0.145);
}

.grid-item:hover {
  transform: scale(1.075);
  z-index: 5;
  box-shadow: none;
  cursor: pointer;
}

.grid-item h3 {
  font-size: 1rem;
  position: relative;
  top: 45%;
  left: 20%;
  color: white;
  text-shadow: 3px 3px #333;
}

.grid-item a img {
  height: 100%;
  width: 100%;
  border-radius: 20px;
  overflow: hidden;
}

a.img:hover {
  background-color: white;
}

@media screen and (max-width: 1530px) {
  .grid-container {
    width: 700px;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 950px) {
  .grid-container {
    width: 500px;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
  }
}

@media screen and (max-width: 620px) {
  .grid-container {
    width: 80%;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
  }
}

footer {
  width: 100%;
  background-color: rgb(128, 148, 250);
  height: 100%;
}

footer p {
  text-align: center;
  padding: 30px;
}

.social {
  text-align: center;
}

.fab {
  color: #fff;
  padding: 30px 30px 20px 30px;
}

.fab:hover {
  background-color: rgb(128, 148, 250);
}

